.wall{
    background: url('https://upload.wikimedia.org/wikipedia/commons/thumb/8/80/Hyades.jpg/1024px-Hyades.jpg');
    background-size: cover;
  }
  
  .scene{
    display: inline-block;
    vertical-align: middle;
    -webkit-perspective: 5px;
            perspective: 5px;
    -webkit-perspective-origin: 50% 50%;
            perspective-origin: 50% 50%;
    position: relative;
  }
  
  .wrap{
    position: absolute;
    width: 1000px;
    height: 1000px;
    left: -500px;
    top: -500px;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    -webkit-animation: move 12s infinite linear;
            animation: move 12s infinite linear;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
  }
  
  .wrap:nth-child(2){
    -webkit-animation: move 12s infinite linear;
            animation: move 12s infinite linear;
    -webkit-animation-delay: 6s;
            animation-delay: 6s;
  }
  
  .wall {
    width: 100%;
    height: 100%;
    position: absolute;
    opacity: 0;
    -webkit-animation: fade 12s infinite linear;
            animation: fade 12s infinite linear;
    -webkit-animation-delay: 0;
            animation-delay: 0;
  }
  
  .wrap:nth-child(2) .wall {
    -webkit-animation-delay: 6s;
            animation-delay: 6s;
  }
  
  .wall-right {
    -webkit-transform: rotateY(90deg) translateZ(500px);
            transform: rotateY(90deg) translateZ(500px);
  }
  
  .wall-left {
    -webkit-transform: rotateY(-90deg) translateZ(500px);
            transform: rotateY(-90deg) translateZ(500px);
  }
  
  .wall-top {
    -webkit-transform: rotateX(90deg) translateZ(500px);
            transform: rotateX(90deg) translateZ(500px);
  }
  
  .wall-bottom {
    -webkit-transform: rotateX(-90deg) translateZ(500px);
            transform: rotateX(-90deg) translateZ(500px);
  }
  
  .wall-back {
    -webkit-transform: rotateX(180deg) translateZ(500px);
            transform: rotateX(180deg) translateZ(500px);
  }
  
  @-webkit-keyframes move {
    0%{
      -webkit-transform: translateZ(-500px)  rotate(0deg);
              transform: translateZ(-500px)  rotate(0deg);
    }
    100%{
      -webkit-transform: translateZ(500px) rotate(0deg);
              transform: translateZ(500px) rotate(0deg);
    }
  }
  
  @keyframes move {
    0%{
      -webkit-transform: translateZ(-500px)  rotate(0deg);
              transform: translateZ(-500px)  rotate(0deg);
    }
    100%{
      -webkit-transform: translateZ(500px) rotate(0deg);
              transform: translateZ(500px) rotate(0deg);
    }
  }
  
  @-webkit-keyframes fade {
    0%{
      opacity: 0;
    }
    25% {
      opacity: 1;
    }
    75% {
      opacity: 1;
    }
    100%{
      opacity: 0;
    }
  }
  
  @keyframes fade {
    0%{
      opacity: 0;
    }
    25% {
      opacity: 1;
    }
    75% {
      opacity: 1;
    }
    100%{
      opacity: 0;
    }
  }
